.YouTubePopUp-Content {
  width: 680px;
  margin: 0 auto;
  position: relative; }

.YouTubePopUp-Content:after {
  padding-top: 56.25%;
  /* 16:9 ratio */
  display: block;
  content: ""; }

.YouTubePopUp-Content iframe {
  max-width: 100% !important;
  width: 100% !important;
  display: block !important;
  height: 100% !important;
  border: none !important;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35); }

.YouTubePopUp-Hide {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: YouTubePopUpHide;
  animation-name: YouTubePopUpHide; }

@-webkit-keyframes YouTubePopUpHide {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes YouTubePopUpHide {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.line-through {
  text-decoration: line-through; }

.YouTubePopUp-Close {
  position: absolute;
  top: 0;
  cursor: pointer;
  bottom: 125%;
  right: 0px;
  margin: auto 0;
  width: 38px;
  height: 38px;
  opacity: 0.3;
  background-image: url(../assets/img/popup_close.png);
  background-size: 38px 38px;
  -webkit-background-size: 38px 38px;
  -moz-background-size: 38px 38px;
  -o-background-size: 38px 38px;
  transition: opacity 0.25s ease; }

.YouTubePopUp-Close:hover {
  opacity: 0.8; }

.YouTubePopUp-Close:active {
  opacity: 1; }

@media all and (max-width: 800px) {
  .YouTubePopUp-Content {
    max-width: 90%; } }

/* ========================================================================== */
/*   Portfolio Filter
/* ========================================================================== */
ul.portfolio-filter {
  list-style: none;
  margin: 0px;
  padding: 0px; }
  ul.portfolio-filter li {
    display: inline-block;
    margin: auto 15px auto 15px; }

/* ========================================================================== */
/*   Portfolio
/* ========================================================================== */
.portfolio {
  position: relative;
  overflow: hidden; }
  .portfolio:hover .hover-box {
    transform: translateY(0%);
    -webkit-transform: translateY(0%);
    -moz-transform: translateY(0%);
    -o-transform: translateY(0%);
    -ms-transform: translateY(0%);
    -webkit-transition: all 0.2s ease;
    -moz-transition: all 0.2s ease;
    transition: all 0.2s ease;
    opacity: 1;
    visibility: visible;
    display: block; }
  .portfolio .hover-box {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    padding: 30px;
    transform: translateY(100%);
    -webkit-transform: translateY(100%);
    -moz-transform: translateY(100%);
    -o-transform: translateY(100%);
    -ms-transform: translateY(100%);
    -webkit-transition: all 0.2s ease;
    -moz-transition: all 0.2s ease;
    transition: all 0.2s ease;
    visibility: hidden;
    opacity: 0;
    text-align: center; }
    .portfolio .hover-box.white {
      background-color: rgba(255, 255, 255, 0.8);
      color: #000; }
      .portfolio .hover-box.white a {
        color: #000; }
    .portfolio .hover-box.dark {
      background-color: rgba(85, 85, 85, 0.95);
      color: #fff; }
      .portfolio .hover-box.dark a {
        color: #fff; }
    .portfolio .hover-box .flex-center {
      position: relative;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%); }

