}

/*--- Hover Option ---*/

.with-hover {
	position: relative;
	overflow: hidden;
	&:hover {
		.hover-option {
			@include transform(0%);
			@include transition(0.2s);
			opacity: 1;
			visibility: visible;
			display: block;
		}
	}
}

.hidden {
	overflow: hidden
}

.hover-option {
	@include transform(100%);
	@include transition(0.2s);

	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	visibility: hidden;
	opacity: 0;
}

@for $i from 0 through 10 {
	.opacity-hover-#{$i} {
		opacity: $i / 10 !important;
		&:hover {
			opacity: 1 !important;
		}
	}
}

/*------------------------------------------------------------------------*
/* Cart           --------------------------------------------*
------------------------------------------------------------------------- */

.cart-model {
	display: none;
	position: absolute;
	right: 0;
	top: 48px;
	width: 320px;
	background-color: rgba(255, 255, 255, .95);
	border: 1px solid #eee;
	.media {
		border-bottom: 1px solid #eee;
		padding: 20px;
		.media img {
			border-radius: 5px;
		}
		h5 {
			margin-bottom: 5px;
			a {
				font-size: 13px;
				text-transform: uppercase;
				font-weight: 500;
				color: #2b2b2a;
			}
		}
		h6 {
			color: #9b9b9b;
			font-size: 13px;
			text-transform: lowercase;
			font-weight: 400;
			font-family: $secondFont;
			letter-spacing: -.5px;
		}
	}
	a.close {
		font-size: 16px;
		margin-top: 10px;
		color: #ccc;
		&:hover {
			color: $thirdColor;
		}
	}
	a.btn-primary {
		background-color: $colorGrey2;
		border: none;
		font-weight: 300;
		text-transform: capitalize;
		font-size: 14px;
		margin-bottom: 15px;
		border-radius: 0px;
	}
	a.btn-outline-primary {
		border-color: $mainColor;
		color: $mainColor;
		font-weight: 300;
		text-transform: capitalize;
		font-size: 14px;
		border-radius: 0px;
		&:hover {
			background-color: $mainColor;
			color: #fff
		}
	}
	.cart-total {
		margin: 0px 0 20px 0;
		.desc {
			float: left;
			text-transform: capitalize;
		}
		.price {
			float: right;
		}
	}
}


