/* Search Bar           --------------------------------------------*
------------------------------------------------------------------- */

#search_bar {
	position: relative;
	display: none;
	input.input-search {
		margin-bottom: 5px;
		padding: 10px 20px 10px 20px;
		border-radius: 3px;
		position: relative
	}
	a.close-search {
		position: absolute;
		right: 20px;
		top: 8px;
		font-size: 15px;
		z-index: 9
	}
}


