/*------------------------------------------------------------------------*
/* Search Filter           -----------------------------------------------*
------------------------------------------------------------------------- */

@mixin bookIcon($fontIcon) {
	content: $fontIcon;
	font-family: FontAwesome;
	position: absolute;
	top: 5px;
	left: 15px;
	z-index: 5;
	color: $secondColor;
	z-index: 1;
}


.search-filter {
	.nav-tabs .nav-link {
		color: $ColorDark;
		border-radius: 0;
		text-transform: uppercase;
		font-weight: 600;
		border: 1px solid rgba(255, 255, 255, .3);
	}
	.nav-item.show .nav-link,
	.nav-tabs .nav-link.active {
		color: #fff;
		background-color: $mainColor;
		border: 1px solid rgba(255, 255, 255, .3);
	}
	input {
		position: relative;
		padding-left: 35px
	}
	.date-input {
		position: relative;
		&:before {
			@include bookIcon("\f133");
		}
	}
	.destination {
		position: relative;
		&:before {
			@include bookIcon("\f041");
		}
	}
	.rooms {
		position: relative;
		&:before {
			@include bookIcon("\f236");
		}
	}
	.children {
		position: relative;
		&:before {
			@include bookIcon("\f1ae");
		}
	}
}



/*------------------------------------------------------------------------*
/* Section Style           -----------------------------------------------*
------------------------------------------------------------------------- */

section {}

.map-ba {
	background-image: url(../images/map_ba.png);
	background-position: left top;
	background-repeat: no-repeat
}

section .section-title {
	text-align: center;
	margin-bottom: 60px;
}

section .section-title h1 {
	text-transform: capitalize;
	font-weight: 700;
	font-size: 25px;
	color: $thirdColor
}

section .section-title h1 span {
	color: $mainColor;
}

section .section-title span.section-title-des {
	position: relative;
	display: block
}

section .section-title-left {
	text-align: left
}

section .section-title-left span.section-title-des:after {
	text-align: left;
	content: "  ";
	position: relative;
	display: block;
	width: 65px;
	height: 10px;
	border-bottom: solid 1px $secondColor;

}

section .section-title-center span.section-title-des:after {
	content: "  ";
	position: relative;
	display: block;
	width: 65px;
	height: 10px;
	border-bottom: solid 1px $secondColor;
	margin: auto
}


/*----------------------------------------------------------------*
/* Hotel  Cart     -----------------------------------------------*
----------------------------------------------------------------- */

.hotel-grid {
	.hotel-img {
		position: relative;
		overflow: hidden;
	}
	.rating {
		margin-top: -15px;
		position: relative;
		z-index: 2;
	}
}

.rating {
	ul {
		max-width: 160px;
		background-color: #FFF;
		padding: 5px;
		margin-top: 0px;
		margin-right: auto;
		margin-bottom: 0px;
		margin-left: auto;
		-webkit-box-shadow: 0px 0px 8px -1px rgba(89, 89, 89, 1);
		-moz-box-shadow: 0px 0px 8px -1px rgba(89, 89, 89, 1);
		box-shadow: 0px 0px 8px -1px rgba(89, 89, 89, 1);
		text-align: center;
		-webkit-border-radius: 20px;
		-moz-border-radius: 20px;
		border-radius: 20px;
		li {
			display: inline-block;
			background-image: url(../images/star_0.png);
			background-repeat: no-repeat;
			background-position: center center;
			height: 14px;
			width: 14px;
			margin-right: 5px;
			margin-left: 5px;
		}
		li.active {
			background-image: url(../images/star_1.png);
		}
	}
}



