/*--- Widget ---*/

.widget {
	margin-bottom: 20px;
	border: 1px solid $colorGrey1;
	padding: 30px;
	background-color: #fff;
	h4.widget-title {
		text-transform: uppercase;
		font-size: 16px;
		color: $mainColor;
		text-align: center;
		margin-bottom: 20px;
		font-weight: 700;
		position: relative;
		span {
			position: relative;
			display: inline-block;
			vertical-align: top;
			padding: 0 10px;
			background-color: #fff;
		}
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 10px;
			height: 1px;
			width: 100%;
			background-color: $colorGrey1;
		}
	}
	ul.last-posts {
		padding: 0;
		margin: 0;
		list-style: none;
		li {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: solid 1px $colorGrey1;

		}
	}
}

