.widget.widget_recent_entries,
.widget.widget_recent_comments,
.widget.widget_archive,
.widget.widget_categories,
.widget.widget_meta,
.widget.widget_rss {
	ul {
		list-style: none;
		padding-left: 30px;
		li {
			position: relative;
			padding: 5px 0;
