---------------------------------------------------------------- */

.demo-item a {
	color: #333
}

.demo-item:hover a {
	color: $thirdColor
}

.demo-item:hover {
	@include transform(-5%);
	@include transition(0.2s);
}

.demo-item:hover img {
	-webkit-box-shadow: 0px 0px 32px -8px rgba(133, 133, 133, 5);
	-moz-box-shadow: 0px 0px 32px -8px rgba(133, 133, 133, 5);
	box-shadow: 0px 0px 32px -8px rgba(133, 133, 133, 5);
}


#buy-button a {
	font-family: $thirdFont;
	font-weight: 500;
	background-color: #81b441;
	padding: 8px 20px 8px 20px;
	display: inline-block;
	color: #fff;
	position: fixed;
	bottom: 30px;
	z-index: 999;
	right: 45px;
	border-radius: 10px;
	-webkit-animation: color-fade 10s infinite;
	-moz-animation: color-fade 10s infinite;
	animation: color-fade 10s infinite;
}



@-webkit-keyframes color-fade {
	0% {
		background: #9a5342;
	}
	25% {
		background: #81b441;
	}
	50% {
		background: #e46d00;
	}
	75% {
		background: #ff3506;
	}
	100% {
		background: #9a5342;
	}
}

@-moz-keyframes color-fade {
	0% {
		background: #9a5342;
	}
	25% {
		background: #81b441;
	}
	50% {
		background: #e46d00;
	}
	75% {
		background: #ff3506;
	}
	100% {
		background: #9a5342;
	}
}

@keyframes color-fade {
	0% {
		background: #9a5342;
	}
	25% {
		background: #81b441;
	}
	50% {
		background: #e46d00;
	}
	75% {
		background: #ff3506;
	}
	100% {
		background: #9a5342;
	}
}


