.YouTubePopUp-Wrap {
	position: fixed;
	width: 100%;
	height: 100%;
	min-width: 300px;
	background-color: #000;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	z-index: 9999;
	-webkit-animation-duration: 0.5s;
	animation-duration: 0.5s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-name: YouTubePopUp;
	animation-name: YouTubePopUp;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-ms-flex-align: center;
	-webkit-align-items: center;
	-webkit-box-align: center;
	align-items: center;
}

@-webkit-keyframes YouTubePopUp {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes YouTubePopUp {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

.hosting-search-output {
	.form-control {
		background-color: rgba(255, 255, 255, .4);
	}
	.dropdown-toggle::after {
		display: none
	}
	.dropdown-menu {
		a.dropdown-item {
			font-size: 13px;
			font-weight: 600;
			text-transform: uppercase;
			padding: 10px;
			border-bottom: 1px solid rgba(255, 255, 255, .3);
			&:hover {
				color: #fff;
				background-color: $thirdColor;
			}
			.fa {
				color: #fff;
				margin-right: 15px;
			}
		}
	}
	.form-control-lg,
	.input-group-lg > .form-control,
	.input-group-lg > .input-group-addon,
	.input-group-lg > .input-group-btn > .btn {
		font-size: 15px;
		text-transform: lowercase;
		font-weight: 300;
		padding: 15px 25px 15px 25px;
		font-family: $mainFont
	}

}

.YouTubePopUp-Content {
	width: 680px;
	margin: 0 auto;
	position: relative;
}

.YouTubePopUp-Content:after {
	padding-top: 56.25%;
	/* 16:9 ratio */
	display: block;
	content: "";
}

.YouTubePopUp-Content iframe {
	max-width: 100% !important;
	width: 100% !important;
	display: block !important;
	height: 100% !important;
	border: none !important;
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto 0;
	-webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35);
	-moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35);
	box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.35);
}

.YouTubePopUp-Hide {
	-webkit-animation-duration: 0.5s;
	animation-duration: 0.5s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-name: YouTubePopUpHide;
	animation-name: YouTubePopUpHide;
}

@-webkit-keyframes YouTubePopUpHide {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes YouTubePopUpHide {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

.line-through {
	text-decoration: line-through
}

.YouTubePopUp-Close {
	position: absolute;
	top: 0;
	cursor: pointer;
	bottom: 125%;
	right: 0px;
	margin: auto 0;
	width: 38px;
	height: 38px;
	opacity: 0.3;
	background-image: url(../assets/img/popup_close.png);
	background-size: 38px 38px;
	-webkit-background-size: 38px 38px;
	-moz-background-size: 38px 38px;
	-o-background-size: 38px 38px;
	transition: opacity 0.25s ease;
}

.YouTubePopUp-Close:hover {
	opacity: 0.8;
}

.YouTubePopUp-Close:active {
	opacity: 1;
}

@media all and (max-width: 800px) {
	.YouTubePopUp-Content {
		max-width: 90%;
	}
}

/* ========================================================================== */

/*   Portfolio Filter
/* ========================================================================== */

ul.portfolio-filter {
	list-style: none;
	margin: 0px;
	padding: 0px;
	li {
		display: inline-block;
		margin: auto 15px auto 15px;
	}
}


/* ========================================================================== */

/*   Portfolio
/* ========================================================================== */

.portfolio {
	position: relative;
	overflow: hidden;
	&:hover {
		.hover-box {
			@include transform(0%);
			@include transition(0.2s);
			opacity: 1;
			visibility: visible;
			display: block;
		}
	}
	.hover-box {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 2;
		width: 100%;
		height: 100%;
		padding: 30px;
		@include transform(100%);
		@include transition(0.2s);
		visibility: hidden;
		opacity: 0;
		text-align: center;
		&.white {
			background-color: rgba(255, 255, 255, .8);
			color: #000;
			a {
				color: #000;
			}
		}
		&.dark {
			background-color: rgba(85, 85, 85, .95);
			color: #fff;
			a {
				color: #fff;
			}
		}
		.flex-center {
			@include vertical-align();
		}
	}
}

/* ========================================================================== */

/*   Hover Effect
/* ========================================================================== */

.hvr-sh2 {
	box-shadow: none;
	&:hover {
		-webkit-box-shadow: 0px 0px 48px -18px rgba(0, 0, 0, 0.75);
		-moz-box-shadow: 0px 0px 48px -18px rgba(0, 0, 0, 0.75);
		box-shadow: 0px 0px 48px -18px rgba(0, 0, 0, 0.75);
	}
}

.lang-dropdown {
	.dropdown-menu.show {
		border-radius: 0px;
		top: 5px !important;
		margin: 0px;
	}
	.dropdown-item {
		padding: 5px 10px 5px 10px !important;
	}
}

